"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { ArrowLeft } from "lucide-react"
import Link from "next/link"

// Mock data - in real app this would come from a database
const mockSystems = [
  { id: "engine", name: "Engine Management System" },
  { id: "abs", name: "ABS/Brake System" },
  { id: "transmission", name: "Transmission Control" },
]

const mockDiagram = {
  id: "engine",
  title: "Engine Management System Wiring",
  description: "Main wiring diagram for MP3 Engine Management System",
  components: [
    { id: "ecu", name: "Engine Control Unit", x: 150, y: 100 },
    { id: "maf", name: "Mass Air Flow Sensor", x: 300, y: 150 },
    { id: "knock", name: "Knock Sensor", x: 450, y: 200 },
  ]
}

export default function WiringDiagrams() {
  const [selectedSystem, setSelectedSystem] = useState<string>("")
  const [diagram, setDiagram] = useState<any>(null)

  const handleSystemSelect = (value: string) => {
    setSelectedSystem(value)
    // In a real app, this would fetch the actual diagram data
    setDiagram(mockDiagram)
  }

  return (
    <main className="min-h-screen p-6 md:p-12 bg-zinc-950">
      <div className="max-w-4xl mx-auto">
        <div className="mb-8">
          <Link href="/">
            <Button variant="ghost" className="text-zinc-400 hover:text-white">
              <ArrowLeft className="mr-2 h-4 w-4" />
              Back
            </Button>
          </Link>
        </div>

        <h1 className="text-3xl font-bold text-white mb-8">Wiring Diagrams</h1>

        <div className="mb-8">
          <Select onValueChange={handleSystemSelect}>
            <SelectTrigger className="w-full md:w-[300px] bg-zinc-900 border-zinc-800">
              <SelectValue placeholder="Select a system" />
            </SelectTrigger>
            <SelectContent className="bg-zinc-900 border-zinc-800">
              {mockSystems.map((system) => (
                <SelectItem key={system.id} value={system.id}>
                  {system.name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        {diagram && (
          <Card className="p-6 bg-zinc-900 border-zinc-800">
            <h2 className="text-2xl font-semibold text-yellow-500 mb-4">
              {diagram.title}
            </h2>
            <p className="text-zinc-400 mb-6">{diagram.description}</p>

            {/* Interactive Wiring Diagram Canvas */}
            <div className="relative w-full h-[500px] bg-zinc-800 rounded-lg mb-6">
              {diagram.components.map((component: any) => (
                <div
                  key={component.id}
                  className="absolute p-2 bg-yellow-500 text-black rounded cursor-pointer hover:bg-yellow-400 transition-colors"
                  style={{
                    left: component.x,
                    top: component.y,
                  }}
                >
                  {component.name}
                </div>
              ))}
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <h3 className="text-lg font-medium text-zinc-400 mb-2">Legend</h3>
                <ul className="space-y-2">
                  <li className="flex items-center">
                    <div className="w-4 h-4 bg-yellow-500 mr-2"></div>
                    <span className="text-white">Power Supply</span>
                  </li>
                  <li className="flex items-center">
                    <div className="w-4 h-4 bg-blue-500 mr-2"></div>
                    <span className="text-white">Signal Input</span>
                  </li>
                  <li className="flex items-center">
                    <div className="w-4 h-4 bg-green-500 mr-2"></div>
                    <span className="text-white">Signal Output</span>
                  </li>
                </ul>
              </div>

              <div>
                <h3 className="text-lg font-medium text-zinc-400 mb-2">Components</h3>
                <ul className="space-y-2">
                  {diagram.components.map((component: any) => (
                    <li key={component.id} className="text-white">
                      {component.name}
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </Card>
        )}
      </div>
    </main>
  )
}
